<template>
  <div class="main">
    <div class="header-warp" @mouseenter="enter" @mouseleave="headerHidden">
      <transition enter-active-class="slideInDown" leave-active-class="slideOutUp">
          <dHeader class="header animated" v-show="show2"  @openModel="openModel"></dHeader>
      </transition>
    </div>
    <router-view/>
     <!-- 设置规则 -->
    <szgz :szgzStatus='szgzStatus' @closeSz="closeSz" v-if="szgzStatus"></szgz>
    <!-- 显示设置 -->
    <xssz :xsszStatus='xsszStatus' @closeSz="closeSz" v-if="xsszStatus"></xssz>
  </div>
</template>

<script>
import dHeader from "../components/header.vue"
import szgz from "../components/monitor/szgz.vue"
import xssz from "../components/monitor/xssz.vue"
export default {
  data(){
      return {
          show2:false,
          szgzStatus:false,
          xsszStatus:false,
          setTime:""
      }
  },
  components:{
      dHeader,
      xssz,
      szgz
  },
  methods:{
    enter(){
      this.show2 = true
      clearTimeout(this.setTime)
    },
    headerHidden(){
      var that = this
      that.setTime=setTimeout(()=>{
        that.show2 = false
      },1000)
    },
    openModel(data){
      if(data=="设置"){
        this.szgzStatus =true
      }else if(data=="显示"){
        this.xsszStatus = true
      }
      console.log(data)
    },
    closeSz(data){
      if(data=="设置"){
        this.szgzStatus = false
      }else if(data=="显示"){
        this.xsszStatus = false
      }
    }
  }
}
</script>
<style scoped>
.main{
    display: flex;
    align-items: center;
    flex-flow: column;
    background: #122540;
    min-height: 100%;
  }
 .header{
    position: absolute;
    top: 0;
  }
  .header-warp{
    height: 40px;
    width: 100%;
    position: relative;
    z-index: 130;
  }
</style>



