import { getPortPromise } from "portfinder";
import {monitor,rule} from '@/api/monitor'
const state={   //要设置的全局访问的state对象
    mainData: {
        "machineTypeList":[],
        "machinesInfoVOList":[],
        "monitorVOList":[]
    },
    DataLength:0,
    selectIndex:0,
    departmentList:[],
    rulList:[],//规则
    rotuerName:"",
    startBedId:null,
    endBedId:null,
    selectTem:true//选择体温
    //要设置的初始属性值
  };
const getters = {   //实时监听state值的变化(最新状态)
   watchData(state){
    return state.mainData
   },
   departmentId(state){//获取选择科室的id
    return state.departmentList[state.selectIndex].departmentId
   },
   monitorVOList(state){//获取健康信息数组
    return state.mainData.monitorVOList
   },
   ruleTwHeight(state){
       if(state.selectTem){
            return state.rulList.filter((item)=>{
                return item.type ===0
            })[0].highAlert
       }else{
            return (((state.rulList.filter((item)=>{
                return item.type ===0
            })[0].highAlert*9)/5) + 32).toFixed(2)
       }
   },
   ruleTwLow(state){
    if(state.selectTem){
        return state.rulList.filter((item)=>{
                    return item.type ===0
                })[0].lowAlert
        }else{
                return (((state.rulList.filter((item)=>{
                    return item.type ===0
                })[0].lowAlert*9)/5) + 32).toFixed(2)
        }
    }
};
const mutations = {
   changeselectTem(state,data){
    state.selectTem = data
   },
   setFilterBedId(state,data){
       console.log(data)
    state.startBedId = data.startBedId,
    state.endBedId = data.endBedId
   },
   getData(state,data){
     state.mainData.machineTypeList = data.machineTypeList
     state.mainData.machinesInfoVOList = data.machinesInfoVOList
     state.mainData.monitorVOList.splice(0,data.monitorVOList.length,...data.monitorVOList)
     function compare(property){//素组进行排序
                return function(obj1,obj2){
                    var value1 = obj1[property];
                    var value2 = obj2[property];
                    if(value1=="null"||value2=="null"){
                        if(value1=="null"){
                            return 10;
                        }else{
                            return -10
                        }
                    }else{
                        return value1-value2
                    }
                }
        }
     if(data.monitorVOList.length>=state.mainData.monitorVOList.length){//数组进行替换 记得替换数组前要进行数字的初始化
        state.mainData.monitorVOList = data.monitorVOList
     }else{
        state.mainData.monitorVOList.splice(0,data.monitorVOList.length,...data.monitorVOList)
     }
     state.mainData.monitorVOList = state.mainData.monitorVOList.sort(compare("bedId"))//数组排序
     if(!state.selectTem){
        state.mainData.monitorVOList.map((item,index)=>{
            console.log(item.monitorTemperatureVO.temperature)
             if((!isNaN(Number(item.monitorTemperatureVO.temperature)))&&item.monitorTemperatureVO.temperature){//如何转化为华摄度
                console.log(item.monitorTemperatureVO)
                state.mainData.monitorVOList[index].monitorTemperatureVO.temperature = (((item.monitorTemperatureVO.temperature*9)/5) + 32).toFixed(2)
             }
        })
     }
     console.log(state.mainData.monitorVOList[1].monitorTemperatureVO)
   },
   initialData(state,data){
     state.mainData.monitorVOList=[]
     var initialObj ={
            "bedId": "null",
            "bedName": "null",
            "caseNum": "",
            "errorStatus":0,
            "patientName": "",
            "sleepingLeaveTimes": "",
            "sleepingStatus":"",
            "monitorBloodVO": {
            },
            "monitorRespiratoryRateVO": {
            },
            "monitorHeartRateVO": {
            },
            "monitorTemperatureVO": {
            }
       }
       for(var i=0;i<data;i++){
        state.mainData.monitorVOList.push(initialObj)
       }
   },
   changeSelect(state,data){
         state.selectIndex = data
   },
   addData(state,data){
    console.log(data)
    state.mainData.push(data)
    console.log(state.mainData)
  },
  getDepartmentList(state,data){
    state.departmentList = data;
  },
  getRul(state,data){//获取规则
    state.rulList = data;
  },
  changeRotuerName(state,data){
    state.rotuerName = data
  }
};
const actions = {
    refreshData({dispatch,getters}){//刷新数据  刷新数据的时候不需要改变departmentId所以不传
        dispatch('getData',getters.departmentId)
    },
    getData({commit,getters,state},data){//获取当前已使用医用 并进行排序
        var  departmentId =""
        if(data){
            departmentId =data
        }else{
            departmentId=getters.departmentId
        }
        monitor({
            "departmentId":departmentId,
            "start":state.startBedId,
            "end":state.endBedId
        }).then((res)=>{
            if(res.code==200&&res.success){
                console.log(res.data)
                commit('initialData',res.data.bedCount)
                commit('getData',res.data)
            }
        })
    },
    // initialData({commit,rootGetters,state},data){//获取有多少床位
    //     if(data){//要改变departmentId
    //        return getBedCount({//获取床位数量  返回一个promise
    //             departmentId:data
    //         }).then((res)=>{
    //             if(res.code==200&&res.success){
    //                 commit('getBedData',res.data)
    //                 commit('initialData',res.data)
    //             }
    //             console.log(res)
    //         })
    //     }else{
    //         if(state.bedCount){
    //               commit('initialData',state.bedCount)
    //         }else{
    //          getBedCount({//获取床位数量
    //                 departmentId:rootGetters["monitor/departmentId"]
    //             }).then((res)=>{
    //                 if(res.code==200&&res.success){
    //                     commit('getBedData',res.data)
    //                     commit('initialData',res.data)
    //                 }
    //                 console.log(res,"www")
    //             }) 
    //         }
    //     }
    // },
    getRul({commit,rootGetters}){//获取规则
        return rule({
            departmentId:rootGetters["monitor/departmentId"]
          }).then((res)=>{
            console.log(res)
            commit('getRul',res.data)
          })
    },
    changeSelect({commit},data){
        commit('changeSelect',data)
    },
    getDepartmentListA({commit},data){
       commit('getDepartmentList',data)
   },
   changeRotuerName({commit},data){
       console.log(data)
      commit('changeRotuerName',data)
   }
};
export default {
   namespaced: true, //用于在全局引用此文里的方法时标识这一个的文件名
   state,
   getters,
   mutations,
   actions
}



