// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'//引入store
import ElementUI from 'element-ui'
import global from './global'
import 'element-ui/lib/theme-chalk/index.css';
import 'element-ui/lib/theme-chalk/display.css';
import 'normalize.css/normalize.css' // a modern alternative to CSS resets
import './permission' // 权限控制
import VueI18n from 'vue-i18n'        //引入vue-i18n (中英文切换)
import echarts from 'echarts/index.js'
import animate from 'animate.css'
import './icon/iconfont.css'//icon的样式
import './assets/scss/common.scss'//icon的样式
import i18n from './utils/i18n';
import locale from 'element-ui/lib/locale/lang/en'
Vue.use(animate)
Vue.use(VueI18n);    //通过插件的形式挂载
Vue.prototype.global = global
Vue.use(ElementUI,{
  i18n: (key, value) => i18n.t(key, value)
})
// Vue.use(ElementUI,{
//   locale
// })
Vue.config.productionTip = false
Vue.prototype.error = error
Vue.prototype.success = success
Vue.prototype.timestampToTime = timestampToTime
Vue.prototype.$echarts = echarts
// Vue.prototype.$get=get;
// Vue.prototype.$post=post;
// Vue.prototype.$put=put;
// Vue.prototype.$delete=deletes;
/*---------使用语言包-----------*/
// const i18n = new VueI18n({
//   locale: store.state.login.language,    // 语言标识
//   messages: {
//     zh: Object.assign(require('@/utils/zh'), zhLocale),  //对象合并进行 。。。。不知道有没有实现
//     en: Object.assign(require('@/utils/en'), enLocale),
//   }
// })
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  store,
  components: { App },
  template: '<App/>'
})

/**
 * 错误和正确提示
 * @param msg
 */
export function error (msg) {
  this.$notify.error({
    // title: i18n.$t('tips.zy'),
    message: msg,
    duration:2000
  });
}

/**
 * 错误和正确提示
 * @param msg
 */
export function success (msg) {
  this.$notify({
    // title: '成功',
    message: msg,
    type: 'success',
    duration:2000
  });
}


export function timestampToTime(timestamp){
  var date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
  var D = (date.getDate()<10?'0'+date.getDate():date.getDate()) + ' ';
  var h = (date.getHours()<10?'0'+date.getHours():date.getHours())+':';
  var m = (date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes())+':';
  var s = date.getSeconds()<10?'0'+date.getSeconds():date.getSeconds();
  return Y+M+D+h+m+s;
}



