<template>
  <div class="zan-dialog clearfix" :class="flow ? 'zan-dialog--show' :'' " >
    <div class="zan-dialog__mask">
    <div class="zan-dialog__container">
      <slot></slot>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'toast',
  data () {
    return {
      ishideModal: false, //模态框的状态  true-隐藏  false-显示
    }
  },
  props: {
     flow: {
       type:Boolean,
       observer: function (news, olds, path) {
         console.log(news, olds)
       }
     },
   },
  methods:{
    hideModal:function(){
      that.ishideModal = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.zan-dialog{
  display:block;
}
.zan-dialog__mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.4);
  display:none;
}
.zan-dialog__container {
  top: 50%;
  z-index: 12;
}
 .zan-dialog__container {
   width:100%;
   height:100%;
   display: flex;
    justify-content: center;
    align-items: center;
}

.zan-dialog--show .zan-dialog__mask {
  display: block;
}
.box{
  background:#fff;
  z-index:10000;
  border-radius:8px;
}
</style>



