import service from '../utils/http'
export function logIn(data) {//登录
  return service({
    url: '/manage/login',
    method: 'post',
    data:data
  })
}
export function logOut(data) {//登出
  return service({
    url: '/manage/login',
    method: 'delete',
    data:data
  })
}



