import Vue from 'vue';
import Router from 'vue-router';
import Login from '../views/login';
import Main from '../views/main';
import store from '@/store'
Vue.use(Router)
//所有权限通用路由表
//如首页和登录页和一些不用权限的公用页面

export const constantRouterMap = [
  { path: '/login', component: Login,name:"login"},
  {
    path: '/',
    component:Main,
    children:[
      {path:'',component: () => import('@/views/monitor'),name:"monitor"},
      {path:'history',component: () => import('@/views/history'),name:"history"},
      {path:'device',component: () => import('@/views/device'),name:"device"},
      {path:'bed',component: () => import('@/views/bed'),name:"bed"},
      {path:'router',component: () => import('@/views/router'),name:"router"},
      {path:'account',component: () => import('@/views/account'),name:"account"},
      {path:'statistics',component: () => import('@/views/statistics'),name:"statistics"}
    ]
  }
]
//实例化vue的时候只挂载constantRouter
export default new Router({
  routes: constantRouterMap
});



