<template>
    <div class="header">
        <div class="header-warp">
            <div class="left">
                <div v-if="selectData==$t('monitor.header.jiance')">
                    <el-tooltip effect="dark" :content="$t('monitor.header.fangda.name')" placement="bottom" v-if="!fullScreen ">
                        <i class="iconfont fangda" @click.stop="requestFullScreen" >&#xe61c;</i>
                    </el-tooltip>
                </div>
                <el-tooltip effect="dark" :content="$t('monitor.header.fangda.name2')" placement="bottom" v-if="fullScreen ">
                        <i class="iconfont fangda" @click.stop="exitFullscreen" >&#xe650;</i>
                </el-tooltip>
                <el-tooltip effect="dark" :content="$t('monitor.header.xianshi.name')" placement="bottom" v-if="selectData==$t('monitor.header.jiance')">
                    <i class="iconfont eyes" @click.stop="showModel('显示')">&#xe624;</i>
                </el-tooltip>
                <el-tooltip effect="dark" :content="$t('monitor.header.shezhi.name')" placement="bottom" v-if="selectData==$t('monitor.header.jiance')">
                    <i class="iconfont shezhi" @click.stop="showModel('设置')">&#xe622;</i>
                </el-tooltip>
            </div>
            <div class="mid">
                <div class="mid-item" v-for="(item,index) in jianData" :key="index" @click.stop="selectRouter(item.font)" :class="selectData==item.font?'changeCloro':''" :style="item.role>=role?'':'display:none'">
                    <div  style="display:flex;align-items: center">
                        <i class="iconfont font2" v-html="item.icon" :class="selectData==item.font?'changeCloro':''"></i>
                         <div>{{item.font}}</div>
                    </div>
                </div>
            </div>
            <div class="right" @click.stop="showExit">
                <img src="../../static/image/touxiang.png" class="circle">
                <i class="iconfont font3" :class="tuichu?'trans':''">&#xe625;</i>
                <div class="exit" v-show="tuichu" @click="exitAccount">
                   <div class="exit-font">
                       {{$t('monitor.header.tuichu')}}
                   </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState,mapActions,mapGetters} from 'vuex'
import { logOut} from '../api/login'
import { getToken} from '@/utils/auth'
export default {
    data(){
        return {
            jianData:[
                {font:this.$t('monitor.header.jiance'),icon:"&#xe633;",role:2},
                {font:this.$t('monitor.header.lishi'),icon:"&#xe61e;",role:2},
                {font:this.$t('monitor.header.shebei'),icon:"&#xe62f;",role:1},
                {font:this.$t('monitor.header.chuangwei'),icon:"&#xe62d;",role:1},
                {font:this.$t('monitor.header.luyou'),icon:"&#xe62c;",role:1},
                {font:this.$t('monitor.header.zhanghao'),icon:"&#xe630;",role:1},
                {font:this.$t('monitor.header.tjgw'),icon:"&#xe635;",role:2}
            ],
            fullScreen : false,
            selectData:this.$t('monitor.header.jiance'),
            tuichu:false//是否显示退出
        }
    },
    computed:{
         ...mapState("monitor",["rotuerName"]),
         ...mapGetters("login",["useDepartId","role"])
    },
    methods:{
        ...mapActions("monitor",["changeRotuerName"]),
        ...mapActions("login",["logeOut"]),
        requestFullScreen() {
            var el = document.documentElement;

            var rfs = el.requestFullScreen || el.webkitRequestFullScreen ||

                el.mozRequestFullScreen || el.msRequestFullScreen;

            if(typeof rfs != "undefined" && rfs) {

                rfs.call(el);

            } else if(typeof window.ActiveXObject != "undefined") {

                //for IE，这里其实就是模拟了按下键盘的F11，使浏览器全屏

                var wscript = new ActiveXObject("WScript.Shell");

                if(wscript != null) {

                    wscript.SendKeys("{F11}");

                }

            }
        },
        exitFullscreen() {
            // 判断各种浏览器，找到正确的方法
            var exitMethod = document.exitFullscreen || document.mozCancelFullScreen || document.webkitExitFullscreen || document.webkitExitFullscreen;
            if (exitMethod) {
                exitMethod.call(document);
            }
            else if (typeof window.ActiveXObject !== "undefined") {//for Internet Explorer
                var wscript = new ActiveXObject("WScript.Shell");
                if (wscript !== null) {
                wscript.SendKeys("{F11}");
                }
            }
        },
        selectRouter(data){
           if(data == this.$t('monitor.header.jiance')){
               this.$router.push({ path: '/' })
           }else if(data == this.$t('monitor.header.lishi')){
                this.$router.push({ path: '/history' })
           }else if(data == this.$t('monitor.header.shebei')){
                this.$router.push({ path: '/device' })
           }else if(data == this.$t('monitor.header.chuangwei')){
                this.$router.push({ path: '/bed' })
           }else if(data == this.$t('monitor.header.luyou')){
                this.$router.push({ path: '/router' })
           }else if(data == this.$t('monitor.header.zhanghao')){
                this.$router.push({ path: '/account' })
           }else if(data == this.$t('monitor.header.tjgw')){
                this.$router.push({ path: '/statistics' })
           }
           this.selectData = data
           this.changeRotuerName(data)
        },
        showExit(){
            this.tuichu = !this.tuichu
        },
        exitAccount(){
            var that = this
            var data={
              token:getToken()
            }
            this.logeOut(data);
        },
        showModel(data){
            this.$emit("openModel",data)
        }
    },
    mounted(){
        var that = this
        var addEvent = "webkitfullscreenchange" || 'fullscreenchange' ||'mozfullscreenchange' ||'msfullscreenchange'
        document.addEventListener(addEvent, function( event ) {
        if (document.fullscreenElement) {
            console.log('进入知全屏');
            that.fullScreen = true
        } else {
            that.fullScreen = false
            console.log('退出全屏');
        }});

    },
    created(){
        console.log(this.rotuerName)
        if(this.rotuerName){
            this.selectData = this.rotuerName
        }
    }
}
</script>
<style lang="scss" scoped>
  .header{
      height: 47px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background:rgba(68,68,68,1);
      position: relative;
      z-index: 10;
  }
  .header-warp{
      width: 96%;
      display: flex;
      justify-content: space-between;
      // background: #122540;
      .left{
          width: 130px;
          height: 25px;
          display: flex;
          justify-content: space-between;
          align-items: center;
      }
      .mid{
          width: 1000px;
          height: 25px;
          display: flex;
          justify-content: space-between;
          align-items: center;
      }
      .mid-item{
          font-size:18px;
          font-weight:500;
          color:rgba(255,255,255,1);
          line-height:25px;
          cursor: pointer;
      }
      .mid-item:hover{
          color: rgba(0, 145, 255, 1);
          .font2{
              color: rgba(0, 145, 255, 1);
          }
      }
      .font2{
          font-size: 14px;
          margin-right: 10px;
      }
      .changeCloro{
          color: rgba(0, 145, 255, 1);
      }
      .right{
          width: 60px;
          height: 25px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-left: 10px;;
          position: relative;
      }
      .circle{
          width:28px;
          height:28px;
          border-radius: 50%;
          background: chartreuse;
          margin-right: 10px;
      }
      .font3{
          font-size: 8px;
          cursor: pointer;
          transition: all 1s;
      }
      .trans{
          transition: all 1s;
          transform: rotate(-180deg);
      }
      .eyes{
          font-size: 14px;
          cursor: pointer;
      }
      .fangda{
          cursor: pointer;
          font-size: 22px;
      }
      .shezhi{
           cursor: pointer;
           font-size: 24px;
      }
  }
  .exit{
        width:73px;
        height:40px;
        background-color:black;
        position: absolute;
        top: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        .exit-font{
         font-size:14px;
         font-weight:500;
         color:#fff;
        }
  }
</style>



