import service from '../utils/http'
export function getDepartmentList(query) {//获取科室
  return service({
    url: '/hospital/departments',
    method: 'get',
    params: query
  })
}
export function getBedCount(query) {//获取床位数量
  return service({
    url: '/hospital/bedCount',
    method: 'get',
    params: query
  })
}
export function monitor(query) {//获取监控数据
    return service({
      url: '/monitor/monitor',
      method: 'get',
      params: query
    })
  }
export function record(query) {//停止监测
    return service({
      url: '/monitor/record',
      method: 'put',
      data: query
    })
}
export function start(query) {//启用设备
  return service({
    url: '/monitor/start',
    method: 'post',
    data: query
  })
} 
export function changebed(query) {//更换床位
  return service({
    url: '/monitor/bed',
    method: 'put',
    data: query
  })
}
export function changeMachine(query) {//更换设备
  return service({
    url: '/monitor/changeMachine',
    method: 'put',
    data: query
  })
}
export function emptyBeds(query) {//获取空床位
  return service({
    url: '/hospital/emptyBeds',
    method: 'get',
    params: query
  })
}
export function monitorEmptyBeds(query) {//获取未启用某设备的空床位
  return service({
    url: '/hospital/monitorEmptyBeds',
    method: 'get',
    params: query
  })
}
export function machineList(query) {//获取空余设备
  return service({
    url: '/machine/machineList',
    method: 'get',
    params: query
  })
}
export function rule(query) {//获取规则
  return service({
    url: '/monitor/rule',
    method: 'get',
    params: query
  })
}
export function setRule(query) {//设置规则
  return service({
    url: '/monitor/setRule',
    method: 'post',
    data:query
  })
} 
export function monitorHistoryData(query) {//获取历史体温记录
  return service({
    url: '/history/monitorHistoryData',
    method: 'get',
    params: query
  })
}
export function filterBeds(query) {//获取床位列表
  return service({
    url: '/hospital/filterBeds',
    method: 'get',
    params: query
  })
}
export function time(query) {//获取服务器时间戳
  return service({
    url: '/monitor/time',
    method: 'get',
    params: query
  })
} 
export function hospitalName(query) {//获取医院名称
  return service({
    url: '/hospital/hospital',
    method: 'get',
    params: query
  })
} 
