import { getToken, setToken, removeToken } from '@/utils/auth'
import { Notification }from 'element-ui'
import { logIn,logOut} from '@/api/login'
import router from '@/router'
const state={   //要设置的全局访问的state对象
    token: getToken(),
    userInfo:{},
    language:""
    //要设置的初始属性值
  };
const getters = {   //实时监听state值的变化(最新状态)
   newToken(state) {  //承载变化的showFooter的值
      return state.token
   },
   useDepartId(state){
      return state.userInfo.departmentId
   },
   role(state){
      return state.userInfo.role
   }
};
const mutations = {
   getToken(state,token) {   //自定义改变state初始值的方法，这里面的参数除了state之外还可以再传额外的参数(变量或对象);
      state.token = token;
      setToken(token)
      router.push({name:"monitor"})
   },
   setUserInfo(state,userInfo) {
      state.userInfo = userInfo;
      console.log(state.userInfo)
   },
   removeToken(state) {  //同上
     console.log(state)
       state.token = ""
       removeToken()
       router.push({name:"login"})
   },
   setLanguage(state,language){
     state.language = language
   },
};
const actions = {
  setLanguage({commit},data){
    commit('setLanguage',data)
  },
   logeIn({commit,state},data) {  //自定义触发mutations里函数的方法，与store 实例具有相同方法和属性
      logIn(data).then((res)=>{
        console.log(res)
        var data = res
        console.log(res)
        if(data.code==200 && data.success){
          console.log(data.data.token)
          commit('getToken',data.data.token)
          commit('setUserInfo',data.data)
          console.log(state.language)
          if(state.language=='en'){
             Notification({
               title: 'Success',
               message: 'Login Successful',
               type: 'success',
               duration:1000
             });
          }else{
            Notification({
               title: '成功',
               message: '登录成功',
               type: 'success',
               duration:1000
             });
          }
        }else{
            // if(state.language=='en'){
            //    Notification.error({
            //       title: 'Note',
            //       message: data.message,
            //       duration:1000
            //   });
            // }else{
            //    Notification.error({
            //       title: '注意',
            //       message: data.message,
            //       duration:1000
            //   });
            // }
        }
      })
   },
   logeOut({commit,state},data) {  //同上注释
     logOut(data).then((res)=>{
       var data = res;
       console.log(res.code)
       if(data.code=='200' && data.success){
         commit('removeToken');
         if(state.language=='en'){
            Notification({
               title: 'Success',
               message: "Exit successfully",
               type: 'success',
               duration:1000
             });
         }else{
            Notification({
               title: '成功',
               message: "退出成功",
               type: 'success',
               duration:1000
             });
         }
       }else{
         if(state.language=='en'){
            Notification.error({
               title: 'Note',
               message: data.message,
               duration:1000
             });
         }else{
            Notification.error({
               title: '注意',
               message: data.message,
               duration:1000
             });
         }
       }
     })


   }
};
export default {
   namespaced: true, //用于在全局引用此文里的方法时标识这一个的文件名
   state,
   getters,
   mutations,
   actions
}



