<template id="home">
  <div class="login-all">
    <div class="login clearfix">
      <div class="login-left">
        <img src="../assets/img/login/icon.png" alt="" class="login-icon">
        <h1>{{$t("login.systemTitle")}}</h1>
        <h2>{{$t("login.companyName")}}</h2>
        <h3>WELCOME</h3>
      </div>
      <div class="login-right">
        <div class="login-right-box">
          <h2>{{$t("login.h1title")}}</h2>
          <form class="login-form">
            <div class="login-title clearfix">
              <a href="javascript:void(0)">{{$t("login.loginMethod")}}</a>
            </div>
            <div class="login-main">
              <div class="login-item">
                <img src="../assets/img/login/zh.png" alt="">
                <span>{{$t("login.account")}}</span>
                <input type="text" name="" value="" v-model="loginInfo.account">
              </div>
              <div class="login-item">
                <img src="../assets/img/login/password.png" alt="">
                <span>{{$t("login.password")}}</span>
                <input type="password" name="" value="" v-model="loginInfo.password" @keyup.enter="login()">
              </div>
              <div class="login-btn" @click="login()">{{$t("login.h1title")}}</div>
              <el-select  v-model='languageName' class='language-select'>
                <el-option
                  v-for="item in languageList"
                  :key="item.index"
                  :label="item.name"
                  :value="item.name"
                  @click.native="changeType(item.enName)">
                </el-option>
              </el-select>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
import { mapState,mapActions} from 'vuex'
var that= {};
export default {
   data(){
     return{
       loginInfo:{
         account:'',
         password:'',
       },
       languageList:[
         {index:0,name:'CN 简体中文',enName:'zh'},
         {index:1,name:'US English',enName:'en'},
       ],
       languageName:'Language | 语言'
     }
   },
   computed:{

   },
   beforeCreate(){
        that = this;
    },
    created(){

    },

    mounted:function(){
      that.getNowLanguage();

    },
    methods:{
      ...mapActions("monitor",["changeRotuerName"]),
      ...mapActions("login",["logeIn","setLanguage"]),
      getNowLanguage(){
        console.log(localStorage.getItem('locale'))
        if(localStorage.getItem('locale')){

        }else{
          localStorage.setItem('locale','zh');
          that.setLanguage('zh');
        }
        that.languageList.forEach(item =>{
          if(item.enName==localStorage.getItem('locale')){
          }else{

          }
        })
      },

      changeType(type){
           localStorage.setItem('locale',type);
           that.setLanguage(type);
           this.$i18n.locale = that.$store.state.login.language;
       },
      /*登录*/
      login(){
        if(that.loginInfo.account==''){
          that.error(that.$t('login.txzh'))
        }else if(that.loginInfo.password==''){
          that.error(that.$t('login.txmm'))
        }else{
          var data = {
            account:that.loginInfo.account,
            password:that.loginInfo.password
          }
          that.logeIn(data)
          that.changeRotuerName(that.$t('monitor.header.jiance'))//初始一下路由导航值
        }
      }
    },
    components:{

    }
  }

</script>

<style scoped lang="scss">

  .clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden}
  .clearfix{zoom:1}
  body,html{
  width:100%;
  height:100%;
  .login-all{
    background:url("../assets/img/login/bg.png") 100% 100%;
    width:100vw;
    height:100vh;
    .login{
      border-radius:30px;
      overflow:hidden;
      position:fixed;
      top:50%;
      left:50%;
      width:57%;
      transform:translate(-50%,-50%);
      display:flex;
      .login-left{
        float:left;
        width:45%;
        background: rgba(255,255,255,0.60);
        .login-icon{
          width:100%;
        }
        h1{
          margin-top:-15px;
          line-height:33px;
          font-family: PingFangSC-Semibold;
          font-size: 28px;
          color: rgba(0,0,0,0.85);
          text-align: center;
          margin-bottom:4px;
        }
        h2{
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: rgba(0,0,0,0.45);
          letter-spacing: 3px;
          line-height: 22px;
          margin-bottom:32px;
          text-align: center;
        }
        h3{
          font-family: PingFangSC-Thin;
          font-size: 28px;
          color: rgba(0,0,0,0.27);
          text-align: center;
          line-height: 40px;
        }
      }
      .login-right{
        float:right;
        width:55%;
        background:#fff;
        height:100%;
        .login-right-box{
          width:61%;
          margin:0 auto;
          h2{
            font-family: PingFangSC-Semibold;
            font-size: 28px;
            color: #1890FF;
            text-align: center;
            margin:100px 0 37px 0;
          }
          .login-form{
            width:100%;
            .login-title{
              a{
                font-family: PingFangSC-Regular;
                font-size: 16px;
                color: #1890FF;
                line-height: 24px;
                float:left;
                padding:8px 20px;
                margin-left:24px;
                border-bottom:2px solid  #1890FF;
                text-decoration:none;
              }
            }
            .login-main{
              width:100%;
              .login-item{
                background: #FFFFFF;
                border: 1px solid #D9D9D9;
                border-radius: 4px;
                height:40px;
                margin-top:24px;
                min-width:230px;
                img{
                  width:16px;
                  height:16px;
                  margin:12px 8px 0 12px;
                  float:left;
                }
                span{
                  font-family: PingFangSC-Regular;
                  font-size:16px;
                  color: rgba(0,0,0,0.25);
                  line-height: 40px;
                  display:block;
                  float:left;
                }
                input{
                  float:left;
                  height:38px;
                  width: 47%;
                  border:0;
                  padding-left:5%;
                  font-family: PingFangSC-Regular;
                  font-size: 16px;
                  line-height: 38px;
                  border:0;
                }
              }
              .login-btn{
                width:100%;
                height:40px;
                background: #1890FF;
                border-radius: 4px;
                margin:70px 0 28px 0;
                border:0;
                font-family: PingFangSC-Regular;
                font-size: 16px;
                color: #FFFFFF;
                line-height: 40px;
                cursor: pointer;
                text-align: center;
              }
              .language-select{
                width:140px;
                height:24px;
                float:right;
                background:rgba(255,255,255,1);
                border-radius:2px;
                border:1px solid rgba(15,140,230,1);
                margin-bottom:57px;
              }
            }
          }
        }
      }
    }
  }
}
.language-select /deep/ .el-input--suffix{
  height:100%!important;
  font-size:12px;
  .el-input__inner{
    height:24px;
    line-height:24px;
    border:none;
    float: left;
    color:rgba(15,140,230,1);
  }
  .el-input__suffix-inner{
    float:left;
    height:24px;
    .el-input__icon{
      line-height:24px;
      float:left;
    }
    .el-icon-arrow-up:before{
      color:rgba(15,140,230,1);
      line-height:24px;
    }
  }
}
</style>



